:root {
    --primary-color: #6c757d;
    --secondary-color: #343a40;
    --border-color: var(--primary-color);
}


.dunnes_stores {
    background-color: darkgreen;
    color: white;
    border: 1px solid #030303; /* border color and width */
    border-radius: 5px; /* make corners rounded */
    padding: 0em .1em;
}

.supervalu {
    background-color: #cd1025;
    color: white;
    border: 1px solid #030303; /* border color and width */
    border-radius: 5px; /* make corners rounded */
    padding: 0em .1em;
}

.tesco {
    background-color: white;
    color: #ee1c25;
    border: 1px solid #030303; /* border color and width */
    border-radius: 5px; /* make corners rounded */
    padding: 0em .1em;
    font-family: 'Verdana', sans-serif; /* Set the custom font as the font-family */
    position: relative; /* Set position to relative for pseudo-element positioning */
}

.tesco::before {
    content: ''; /* Set content to empty to generate pseudo-element */
    position: absolute; /* Set position to absolute for precise positioning */
    top: 88%; /* Position the underline below the existing border */
    left: 3px; /* Position the underline at the left edge of the element */
    width: 90%; /* Set width to 100% to extend the underline across the element */
    border-bottom: 1.6px dashed blue; /* Set border style, width, and color for the underline */
}

.aldi {
    background-color: blue;
    color: white;
    border: 1px solid #030303; /* border color and width */
    border-radius: 5px; /* make corners rounded */
    padding: 0em .1em;
    font-family: 'Verdana', sans-serif; /* Set the custom font as the font-family */
    position: relative; /* Set position to relative for pseudo-element positioning */
}

#results_unordered_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    
    list-style-type: none;
}

.result_list_item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0em .6em;
    margin-bottom: 1em;
    box-sizing: border-box;
    border: 0.1em solid #030303;
    border-radius: 5px;

    cursor: pointer;
}

.result_img {
    border: 1px solid #030303;
    border-radius: 5px;
}

.clicked {
    background-color: #2b8a03;      
    transition: background-color .9s;  /* 1 second transition. This must match ReactJS's timeout in SearchResults.js */
}


@media screen and (min-width: 600px) {
.result_list_item {
    width: calc(50% - 10px);
}
}

@media screen and (min-width: 900px) {
    .result_list_item {
        width: calc(33.33% - 10px);
    }
}


li > img {
    max-width: 300px;  /* or any size you want */
    max-height: 200px; /* or any size you want */
    width: auto;
    height: auto;
}


.currency_dropdown {
    text-align: center;
    display: inline-block;
    width: 100%;
    padding-top: 1em;
    padding-bottom: 1em;
    background-color: #6c757d;
    border: .1em solid black;
}

.currency_flag {
    padding: .21em .1em 0em .1em;

    height: 1em;
    /* Maintain the aspect ratio of the image */
    width: auto;
}
