:root {
    --primary-color: #6c757d;
    --secondary-color: #343a40;
    --border-color: var(--primary-color);
}


#top_page_title {
    background: var(--secondary-color);
    text-align: center;
    font-size: 2em;
    padding: 1em 0em 0em 0em;
    color: white;
    font-size: 2em;
    margin: 0em;
}

#top_nav {
    background: var(--secondary-color);
    text-align: center;
}

#top_anchor {
    background: var(--secondary-color);
    display: inline-block;                        /* Ensures only the image is clickable and not any area left or right of it. */
    text-align: center;
    margin-top: 0.2em;                            /* Puts 0.2 unclickable distance between "Search Product" and the shopping cart*/
    font-size: 3em;                               /* Specifies the size of the shopping cart image/navigation_link*/
}

.error_banner {
    display: block;
    color: white;
    background-color: black;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;

    padding: 20px;
    position: relative;
    box-sizing: border-box;

    /* Create a border with a yellow spaced strip */
    background-image: 
        linear-gradient(to right, red 50%, transparent 50%),
        linear-gradient(to bottom, red 50%, transparent 50%),
        linear-gradient(to left, red 50%, transparent 50%),
        linear-gradient(to top, red 50%, transparent 50%);
    background-size: 10px 2px, 2px 10px;
    background-repeat: repeat-x, repeat-y;
    background-position: top, right, bottom, left;
}