:root {
    --primary-color: #6c757d;
    --secondary-color: #343a40;
    --border-color: var(--primary-color);
}


#search_form_form {
    background-color: var(--secondary-color);
    padding: 1em;
    display: flex;
    align-items: center;
    flex-direction: column;
}

#search_form_form input,
#submit_button {
    width: 80%;
    height: 40px;
    border-radius: 5px;
    padding: .4em 1em;
    font-size: 1.2em;
    box-sizing: border-box;
}

#search_form_form input {
    color: var(--secondary-color);
    background-color: var(--primary-color);
}

#search_form_form input.populated {
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border: var(--border-color) 1px solid;
    transition: background-color 1s ease-in-out, color 1s ease-in-out, border-color 1s ease-in-out;
}

#search_form_form input.not_populated {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border: var(--secondary-color) 1px solid;
    transition: background-color .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;
}

#search_form_form input.not_populated::placeholder {
    color: var(--secondary-color);
}

#submit_button {
    font-size: 1.2em;
    font-weight: inherit;
    border: var(--secondary-color) 1px solid;
    border-radius: 5px;
    background: var(--primary-color);
    color: var(--secondary-color);
    cursor: pointer;
    transition: background-color .18s ease-in-out, color .18s ease-in-out, border-color .18s ease-in-out;
}

#submit_button:hover {
    color: var(--primary-color);
    background-color: var(--secondary-color);
    border: var(--primary-color) 1px solid;
}

#section_loader {
    padding: 0em 0em 1em 0em;
}

.loader {
    border: .5em solid var(--secondary-color);
    border-top: .5em solid var(--primary-color);
    border-radius: 70%;
    width: 1em;
    height: 1em;
    animation: spin 2s linear infinite;
    margin: 1em auto 0em;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}


/* Adjustments for mobile responsiveness */
@media screen and (max-width: 600px) {
    #search_form_form {
        padding: 1em;
    }

    #search_form_form input {
        width: 90%;
    }

    #search_form_form > label {
        font-size: 1em;
    }

    #submit_button {
        width: 90%;
    }
}